.MuiPaper-root:first-child {
  padding: 10px 0;
  margin-bottom: 20px;
}

.MuiPaper-root h3 {
  margin: 0 20px;
  font-weight: 500;
  background-color: #f7f7f7;
  padding: 10px 0;
  border-radius: 6px;
}

.main-info {
  margin: 10px 20px;
  text-align: left;
}

.extra-info {
  margin: 10px 20px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.table-header-cell {
  display: flex;
  align-items: center;
  justify-content: center;
}
.chart-but {
  align-self: center;
  background-color: #e7e7e7;
  margin-left: 5px;
  border-radius: 5px;
  transform: scale(0.8);
}
.chart-but:hover {
  color: #e6e6e6;
  background-color: #b9b9b9;
}

.save-but {
  margin-bottom: 10px !important;
  float: right;
}


