.chart-wrapper {
  position: fixed;
  overflow: auto;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(40, 44, 52, 0.95);
  z-index: 999;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.chart-wrapper canvas {
  max-height: 100%;
  max-width: 100vw;
  color: rgb(130, 197, 255 0.5);
}

.closeBut-wrapper {
  width: 100%;
  position: absolute;
  top: 20px;
  display: flex;
  z-index: 1000;
  justify-content: center;
}

.close-but {
  color: #fafafa !important;
}
