@charset "utf-8";
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: #323232 !important;
  padding: 10px 0;
}

button {
  border: none;
  text-align: center;
  outline: none;
  cursor: pointer;
  transition: 0.2s;
}

li {
  margin: 0;
  padding: 0;
}
ul li {
  list-style: none;
}

.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
}

.input-zone {
  background-color: #fafafa;
  border-radius: 15px;
  padding: 15px;
  margin: 10px 0;
  width: 100%;
}

output span {
  opacity: 0.5;
  padding: 10px;
  font-size: 70%;
}

output div {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 80%;
  background-color: #ececec;
  border-radius: 10px;
  padding: 10px;
}

.input-zone input {
  border: none;
  outline: none;
}

#drop_zone {
  border: 2px rgb(50, 50, 50, 0.5) dashed;
  border-radius: 25px;
  color: rgb(50, 50, 50, 0.5);
  padding: 50px;
}

.instrunctions {
  text-align: left;
}

.istructions-header {
  text-align: center;
  display: flex;
}

.istructions-header button {
  width: 100%;
  padding: 0;
  font-size: 70%;
}
.istructions-body {
  margin-top: 20px;
}

.confirm {
  width: 100%;
  font-size: 80%;
  margin-top: 15px;
  padding: 15px 25px;
  background-color: #ececec;
  border-radius: 10px;
}

.confirm:hover {
  box-shadow: 0 2px 5px #afafaf;
  transform: translateY(-3px);
}

.confirm:active {
  transform: translateY(0);
  box-shadow: none;
}
